<template>
	<List item-layout="vertical" :loading="loading">
		<ListItem v-for="item in listData" :key="item.title">
			<ListItemMeta :title="item.title" :description="item.description" />
			{{ item.content }}
			<template slot="action">
				<li @click="handleToUrl(item.avatar)">check out</li>
			</template>
			<!-- <template slot="extra">
				<img
					src="https://dev-file.iviewui.com/5wxHCQMUyrauMCGSVEYVxHR5JmvS7DpH/large"
					style="width: 280px"
				/>
			</template>-->
		</ListItem>
	</List>
</template>
<script>
	import { getRepos } from "../../request/api.js";
	export default {
		data() {
			return {
				listData: [],
				loading: false
			};
		},
		created() {
			this.getList();
		},
		methods: {
			async getList() {
				if (!localStorage.getItem("cacheListData")) {
					this.loading = true;
					const { data } = await getRepos().catch(_ => (this.loading = false));
					if (data) {
						this.loading = true;
						let list = [...data];
						this.listData = list.map(this.contentFilter);
						localStorage.setItem("cacheListData", JSON.stringify(this.listData));
						this.loading = false;
					}
				} else {
					this.listData = JSON.parse(localStorage.getItem("cacheListData"));
				}
			},
			contentFilter(item) {
				return {
					title: item.name,
					description: item.description,
					avatar: item.svn_url,
					conton: item.description
				};
			},
			handleToUrl(url) {
				this.$Modal.confirm({
					title: "Please Confirm",
					content: "<p>Link to there ?</p>",
					okText: "YES",
					cancelText: "NO",
					onOk: () => window.open(url),
					onCancel: () => {
						// this.$Message.info('Clicked cancel');
					}
				});
			}
		}
	};
</script>